.content {
  position: absolute;
  left: 0;
  top: 80px;
  width: 100%;
  background-color: #EDF9FE;
  margin-top: 0;
  transition: margin-top 0.6s ease;

  @media (min-width: 1024px) {
    top: 100px;
  }

  &--topMargin {
    margin-top: 280px;
    transition: margin-top 0.6s ease;
  }
}

.home {

  &__title {
    margin: 30px auto 40px;
    text-align: center;
    color: #24385D;
    font-size: 1.5em;
    font-weight: 600;
  }
}
